.crt .window-content {
  padding: 5px;
  border: 2px solid var(--aliendarkergreen);
  color: var(--aliengreen);
  text-shadow: 0 0 2px var(--aliendarkergreen);
}

.crt .window-content::before {
  content: ' ';
  display: block;
  position: absolute;
  top: 30px;
  left: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(rgba(18, 16, 16, 0) 50%, rgba(0, 0, 0, 0.25) 50%),
    linear-gradient(90deg, rgba(255, 0, 0, 0.06), rgba(0, 255, 0, 0.02), rgba(0, 0, 255, 0.06));
  z-index: 2;
  background-size: 100% 2px, 3px 100%;
  pointer-events: none;
}

.crt .window-content::after {
  content: ' ';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(18, 16, 16, 0.1);
  opacity: 0;
  z-index: 2;
  pointer-events: none;
  // animation: flicker 0.15s infinite;
}

.crt textarea {
  border-radius: 0px;
  border: 1px solid var(--aliendarkergreen);
  color: ghostwhite;
  font-family: var(--alienchatfont);
  font-size: 14px;
  font-weight: 200;
  letter-spacing: 1px;
}

.crt h1 {
  border-bottom: 2px dotted var(--aliengreen);
  padding-left: 0px;
}

.crt label,
.crt h2,
.crt h3 {
  padding-left: 0px;
  margin: 0px;
}

.crt .profile-img {
  margin: 0;
  height: 157px;
  margin-right: 4px;
  border-radius: 10px;
}

.crt h1.charname input {
  text-transform: uppercase;
  width: calc(100% + 10px);
  padding-left: 8px;
  border: 0px;
}
.crt input[type='text'],
.crt input[type='number'],
.crt input[type='password'],
.crt input[type='date'],
.crt input[type='time'] {
  border-radius: 0px;
  border: 1px solid var(--aliendarkergreen);
}

.crt input[type='text']:hover,
.crt input[type='number']:hover,
.crt input[type='password']:hover,
.crt input[type='date']:hover,
.crt input[type='time']:hover {
  border: 1px solid var(--aliengreen);
}

.crt h1.charname input:focus {
