/* interface */

::-webkit-scrollbar-thumb {
  outline: none;
  border-radius: 2px;
  background: var(--aliendarkergreen);
  border: 1px solid var(--aliendarkergreen);
}

.window-header {
  background-color: rgba(50, 100, 120, 0.3);
  border-radius: 7px 7px 0px 0px;
}

.alienrpg input[type='text'],
.alienrpg input[type='number'],
.alienrpg input[type='password'],
.alienrpg input[type='date'],
.alienrpg input[type='time'] {
  border-radius: 0px;
  border: 1px solid var(--aliendarkergreen);
}

input[type='text']:hover,
input[type='number']:hover,
input[type='password']:hover,
input[type='date']:hover,
input[type='time']:hover {
  border: 1px solid var(--aliengreen);
}

a:hover {
  text-shadow: 0 0 5px var(--aliengreen);
}

#sidebar-tabs {
  border-bottom: 1px solid var(--aliengreen);
  box-shadow: 0 0 10px var(--aliengreen);
}

#sidebar-tabs .item.active {
  border: 1px solid var(--aliengreen);
  box-shadow: 0 0 6px inset var(--aliengreen);
}

select:focus {
  box-shadow: 0 0 5px var(--aliengreen);
}

textarea:focus {
  outline: 0px;
  box-shadow: 0 0 10px var(--aliengreen);
  // background-color: var(--aliengreen);
  color: #000;
  font-weight: 400;
}

combobox:focus {
  outline: 0px;
  box-shadow: 0 0 10px var(--aliengreen);
  background-color: var(--aliengreen);
  color: #000;
}

#hotbar .macro:hover {
  box-shadow: 0 0 10px inset var(--aliengreen);
}

#hotbar .macro.active:hover {
  border: 1px solid var(--aliengreen);
}

#players ol .player.context {
  border-top: 1px solid var(--aliengreen);
  border-bottom: 1px solid var(--aliengreen);
}

#players ol .player.context .player-name {
  text-shadow: 0 0 5px var(--aliengreen);
}

#context-menu li.context-item:hover {
  text-shadow: 0 0 4px var(--aliengreen);
}

#controls .scene-control.active,
#controls .control-tool.active,
#controls .scene-control:hover,
#controls .control-tool:hover {
  border: 1px solid var(--aliengreen);
  border-bottom: 1px solid var(--aliendarkergreen);
  box-shadow: 0 0 10px var(--aliendarkergreen);
}

#navigation #scene-list .scene.view,
#navigation #scene-list .scene.context {
  border: 1px solid var(--aliengreen);
  background: rgba(52, 52, 52, 0.95);
  border-bottom: 1px solid var(--aliengreen);
  box-shadow: 0 0 10px var(--aliendarkergreen);
}

