#pause {
  background: none;
  & > img {
    width: 200px;
    height: 200px;
    top: -50px;
    left: calc(50% - 100px);
    opacity: 0.7;
    --fa-animation-duration: 10s;
  }
  h3 {
    // font-family: var(--alienfont); !
    font-size: 32px;
    text-shadow: 0px 3px 5px rgba(0, 0, 0, 1);
  }
}

.alienrpg #context-menu li.context-item {
  line-height: 1.55;
}

/* CHAT */

.sidebar-tab h1,
.sidebar-tab h2 {
  border-bottom: 2px dotted var(--aliengreen);
}

.sidebar-tab .fas.fa-trash:hover {
  animation: textShadow 0.5s infinite;
}

.chat-message .message-header .message-sender {
  padding-bottom: 5px;
}

.alien-Push-button {
  height: 40px;
  margin-top: 5px;
  margin-left: 1px;
  box-shadow: 0 0 5px var(--aliendarkergreen);
  border-radius: 3px;
  background: url(../images/alien_crt_push-button.png) no-repeat top left;
  color: #00000000;
}

.alien-Push-button:hover {
  border-radius: 3px;
  box-shadow: 0 0 5px var(--aliendarkergreen);
  background-color: inherit;
  background: url(../images/alien_crt_push-button_hover.gif) no-repeat top left;
  color: #00000000;
}

.chat-message,
.chat-message.emote {
  font-family: var(--alienchatfont);
  font-size: 110%;
  font-weight: bold;

  padding: 13px 15px 50px 11px;
  border-width: 0px;

  box-shadow: 0 1px 5px #000;

  background-size: 100%;
  background-position: top, bottom, 0px 18px;
  background-repeat: no-repeat, no-repeat, repeat;
  // background-image: url('../images/chat-top.png'), url('../images/chat-bottom.png'), url('../images/chat-middle.png');
  background-image: url('../images/chat-top.png'), url('../images/chat-bottom.png'), var(--chatbackground);
  // background-image: url('../images/chat-top.png'), url('../images/chat-bottom.png'), url('../images/IndBackground3.png');
}

.chat-message.whisper {
  color: var(--alienchatlightblue);

  padding: 13px 15px 50px 11px;
  border-width: 0px;

  box-shadow: 0 1px 5px #000;

  background-size: 100%;
  background-position: top, bottom, 0px 18px;
  background-repeat: no-repeat, no-repeat, repeat;
  background-image: url('../images/hidden-chat-top.png'), url('../images/hidden-chat-bottom.png'), url('../images/hidden-chat-middle.png');
}

.chat-message.whisper span,
.chat-message.whisper div {
  color: var(--alienchatlightblue);

  // color: royalblue;
}

.chat-message.whisper h2 {
  color: lightblue;
  border-bottom: 2px dotted lightsteelblue;
}

.chat-message.whisper h4 {
  color: lightsteelblue;
}

.chat-message.whisper hr {
  border-top: 1px solid lightsteelblue;
  border-bottom: 1px solid royalblue;
}

.chat-message.whisper .blink {
  color: lightsteelblue;
  background-color: royalblue;
  text-shadow: 0 0 5px lightsteelblue;
  animation: blink 0.5 5;
}

.chat-message.whisper .inline-roll:hover {
  text-shadow: 0 0 5px lightsteelblue;
}

.chat-message.whisper .inline-roll,
.chat-message.whisper .dice-tooltip {
  border-color: lightsteelblue;
}

