  box-shadow: 0 0 5px var(--aliendarkergreen);
  background-color: inherit;
  background: url(../images/alien_crt_push-button_hover.gif) no-repeat top left;
  color: #00000000;
}

.blink {
  padding: 0px 0px 2px 5px;
  margin-top: 5px;
  margin-bottom: 5px;
  // background-color: red;
  color: white;
  text-shadow: 0 0 5px red;
  animation: blink 0.5s 5;
}

.warnblink {
  text-shadow: 0 0 5px red;
  animation: blink 0.5s 5;
}

.message-sender {
  width: 900px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

